<template>
  <div class="tax-page tempate">
    <h4>计税小工具</h4>
    <div class="content">
      <div class="options">
        <span>可以计算劳务报酬、稿酬所得、特权使用费等报表,点击配置自定义模板</span>
      </div>
      <table class="table sample-table table-bordered table-hover">
        <thead>
        <tr>
          <th scope="col">模板名称</th>
          <th scope="col" width="250">操作</th>
        </tr>
        </thead>
        <tbody>
        <tr v-for="item in templates" :key="item.id">
          <td><span>{{item.templateName}}</span></td>
          <td class="td-ops">
            <swak-link v-if="item.id" :to="'/func/template/cfg/' + item.id">配置</swak-link>
            <swak-link v-else :to="'/func/template/add?type=' + item.templeteType">配置</swak-link>
            <swak-link v-if="item.id" @link:click="downloadExcel(item.id)">下载模板</swak-link>
            <swak-link v-if="item.id" @link:click="openUpload(item.id)">导入计算</swak-link>
          </td>
        </tr>
        </tbody>
      </table>
    </div>
    
    <multi-upload :visible.sync="multipartFile.visible" :file="multipartFile"
                  @multiFile:update="uploadSuccess"></multi-upload>
  </div>
</template>

<script>
  import SwakLink from "../../ui/components/link/link";
  import TemplateApi from "@/api/manage/template";
  import ElButton from "element-ui/packages/button/src/button";
  import MultiUpload from "../../ui/components/upload/multiFile";

  export default {
    components: {
      ElButton,
      SwakLink,
      MultiUpload,
    },
    name: "tools",
    data() {
      return {
        templates: [],
        shows: false,
        multipartFile: {
          loading: false,
          visible: false,
          template: null,
          accept: '.xls,.xlsx',
          action: '/api/user/reportcal/save?apiIdent=COMPUTE_TAX'
        },
      }
    },
    mounted() {
      this.$emit('layout:load', "tools")
      this.loadTemplates();
    },
    methods: {
      loadTemplates: function () {
        TemplateApi.tools().then(res => {
          this.templates = res.obj;
        })
      },
      downloadExcel: function (id) {
        TemplateApi.excelOne(id).then(res => {
          this.multipartFile.name = decodeURI(res.fileName)
          this.multipartFile.data = res.data
          this.doDownload();
        }, () => {
          this.$notify.error("error!")
        })
      },
      doDownload: function () {
        let url = window.URL.createObjectURL(new Blob([this.multipartFile.data]))
        let link = document.createElement('a')
        link.style.display = 'none'
        link.href = url
        link.setAttribute('download', this.multipartFile.name)
        document.body.appendChild(link)
        link.click()
      },
      openUpload: function (template) {
        this.multipartFile.loading = true;
        this.multipartFile.visible = true;
        this.multipartFile.template = template;
      },
      uploadSuccess: function () {
      
      },
    }
  }
</script>

