<template>
  <div class="user-table-select">
    <el-dialog
        title="文件上传"
        :visible.sync="visible"
        :before-close="doCancel">
      <div class="multi-file">
        <el-upload
            class="upload-demo"
            drag
            :accept="file.accept"
            :action="action"
            :headers="headers"
            :data="params"
            :on-success="doSuccess">
          <i class="el-icon-upload"></i>
          <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
          <div class="el-upload__tip" slot="tip">只能上传{{file.accept}}文件</div>
        </el-upload>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  import {env} from '@/env'

  export default {
    name: "multi-upload",
    props: {
      visible: {
        type: Boolean,
        default: false
      },
      file: {
        type: Object,
        default: null
      }
    },
    computed: {
      action: function () {
        return env.VUE_APP_BASE_URL + this.file.action;
      },
      headers: function () {
        return {
          'X-Token': this.$store.getters.token
        }
      },
      params: function () {
        return {
          template: this.file.template
        }
      }
    },
    methods: {
      doCancel: function () {
        this.$emit('update:visible', false)
      },
      doSuccess: function (res) {
        if (res.success) {
          this.$notify.success("上传成功");
          this.$emit('multiFile:update', false)
        } else {
          this.$notify.error(res.msg);
        }
      }
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
  @require "../../common/stylus/variable.styl"
  .multi-file {
    text-align center
  }
</style>